@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  height: 100%;
  background-color: #162740;
  margin: 0;
  padding: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: aliceblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-container {
  display: grid;
  grid-template-areas: "title title" "break session" "timer timer";
  grid-column-gap: 15%;
  grid-row-gap: 30px;
  width: 40vw;
  height: 80vh;
  padding: 0;
  text-align: center;
  margin: auto;
  align-items: center;
  justify-items: center;
}

@media (max-width: 769px) {
  .app-container {
    width: 90vw;
    grid-template-areas: "title" "break" "session" "timer";
  }
}

.container {
  background-color: #110f27;
  border: 5px solid white;
  border-radius: 4px;
  font-weight: bold;
  padding: 10px;
  width: 100%;
}
.title {
  grid-area: title;
  font-weight: bold;
  font-size: 48px;
  text-align: center;
}

.break {
  grid-area: break;
}

.session {
  grid-area: session;
}

.timer {
  grid-area: timer;
}

label {
  font-weight: bold;
  font-size: 36px;
  line-height: 48px;
  color: white;
}

.length-button {
  color: #110f27;
  font-weight: bold;
  font-size: 16px;
  background: white;
  border: 5px solid white;
  margin: 5px;
}

.length-value {
  color: #ff7878;
  font-weight: bold;
  font-size: 48px;
  line-height: 75px;
}

.timer-value {
  color: #ff3e3e;
  font-weight: bold;
  font-size: 48px;
}

.timer-button {
  background: white;
  color: #110f27;
  border-radius: 5px;
  margin: 5px;
  border: 0;
}

